<template>
<transition naem="model">
    <div class="modal-mask">
        <div class="modal-wapper">
            <div class="modal-container">
                 <div class="modal-header">
                    <slot name="header">
                        input your name!
                    </slot>
                </div>
                <div class="modal-body">
                   <input v-model="name" class="card-header" @keyup.enter="register">
                <div class="modal-body">
           </div>
       </div>
    </div>
</transition>
</template>

<script>
export default {
    data () {
        return { 
            name: ''
        }
    },
    methods: {
       register: function(event){
           var self = this;
           var data = { 'name': this.name };
           $.ajax({
               type: "POST",
               url: "/api/v1/register",
               contentType: "application/json; charset=utf-8",
               data: JSON.stringify(data)
           })
           .then(
               function (data) {
                    self.$emit('close');
               }
           );
       }
   }
}
</script>

<<style>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 30px 0;
}

.modal-default-button {
  float: right;
}
</style>



